export default {
	data() {
		return {
			offset:0,
			top:[],
			before:true,
			flag:0
		};
	},
	onLoad() {
		this.getMvTop()
	},
	onReachBottom() {
		if(this.before){
			this.getMvTop()
		}
	},
	methods:{
		videoPlay(id){
			this.$request({
				url:"/mv/url?id="+id
			}).then((data)=>{
				if(data.data.code == 200){
					this.src = data.data.data.url
					this.flag = id
					console.log(this.src)
				}
			})
		},
		error(){
			uni.showModal({
				content: '视频加载错误',
				showCancel: false
			})
		},
		getmv(id){
			uni.navigateTo({
				url: `../mvdetails/mvdetails?id=`+id
			})
		},
		getMvTop(){
			this.$request({
				url:'/top/mv?limit=5&offset='+this.offset
			}).then((data)=>{
				if(data.data.code == 200){
					let top = data.data.data
					if(top.length < 5){
						this.before = false
					}
					this.top.push(...top)
					this.offset = this.offset+5
					console.log(this.top ,'最新mv')
				}
			})
		},
	}
}
